@import "normalize.css";
@font-face {
  font-family: "Muller";
  src: local("MullerRegular"), url("fonts/MullerRegular.woff2") format("woff2"), url("fonts/MullerRegular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Muller";
  src: local("MullerLight"), url("fonts/MullerLight.woff2") format("woff2"), url("fonts/MullerLight.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Muller";
  src: local("MullerMedium"), url("fonts/MullerMedium.woff2") format("woff2"), url("fonts/MullerMedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Muller";
  src: local("MullerBold"), url("fonts/MullerBold.woff2") format("woff2"), url("fonts/MullerBold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
:root {
  --yellow-color: #F0BF5F;
  --light-brown-color: #CC9933;
  --brown-color: #A47312;
  --gray-color: #666;
  --light-gray-color: #999;
  --white-color: #fff;
  --dark-white-color: #E9E9E9;
  --light-black-color: #333;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
}

body {
  min-width: 320px;
  font-family: "Muller", sans-serif;
  font-weight: 400;
  background-color: var(--dark-white-color);
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 1200px;
  padding: 0 15px;
  margin: 0 auto;
}

.btn {
  padding: 0;
  cursor: pointer;
  background-color: white;
  border: none;
}

.section:not(:last-child) {
  margin-bottom: 70px;
}
.section__heading {
  margin: 0;
  margin-bottom: 20px;
  padding-left: 45px;
  font-weight: 400;
  font-size: 40px;
  line-height: 40px;
  color: var(--light-black-color);
}

@media (any-hover: hover) {
  .header__telephone:hover {
    color: var(--light-brown-color);
  }
  .header__account-link:hover {
    color: var(--yellow-color);
    fill: var(--yellow-color);
  }
  .header__menu-link:hover {
    color: var(--light-brown-color);
  }
  .header__button:hover {
    color: var(--white-color);
    background-color: var(--light-brown-color);
  }
  .offers__more:hover .offers__more-svg {
    transform: translateX(11px);
  }
  .service__link:hover {
    color: var(--yellow-color);
    stroke: var(--yellow-color);
  }
  .service__link:hover .service__svg {
    transform: translateX(11px);
  }
  .hotel__button:hover {
    color: var(--white-color);
    background-color: var(--light-brown-color);
  }
  .tour__button:hover {
    color: var(--white-color);
    background-color: var(--light-brown-color);
  }
  .contacts__link:hover {
    color: var(--yellow-color);
  }
  .contacts__btn:hover {
    color: var(--white-color);
    background-color: var(--light-brown-color);
  }
  .footer__link:hover {
    color: var(--yellow-color);
  }
  .socials__icon:hover {
    color: var(--light-brown-color);
  }
}
.header {
  padding-top: 30px;
  margin-bottom: 70px;
}
.header__menu {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
}
.header__logo {
  margin-right: 40px;
}
.header__logo-img {
  width: 261px;
  height: 54px;
}
.header__info {
  max-width: 715px;
  display: flex;
  flex-wrap: wrap;
  margin-right: auto;
}
.header__telephone {
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  color: var(--gray-color);
  transition: color 0.2s linear;
}
.header__telephone:focus-visible {
  color: var(--light-brown-color);
  outline: 1px solid var(--light-brown-color);
  outline-offset: 5px;
}
.header__telephone:active {
  color: var(--brown-color);
  outline: none;
}
.header__account-link {
  display: inline-flex;
  align-items: center;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: var(--light-brown-color);
  fill: var(--light-brown-color);
  transition: color 0.2s linear, fill 0.2s linear;
}
.header__account-link:focus-visible {
  color: var(--light-brown-color);
  fill: var(--light-brown-color);
  outline: 1px solid var(--light-brown-color);
  outline-offset: 5px;
}
.header__account-link:active {
  color: var(--brown-color);
  fill: var(--light-brown-color);
  outline: none;
}
.header__account-svg {
  margin-right: 7px;
}
.header__nav {
  padding: 14px 45px;
  display: flex;
  align-items: center;
  background-color: var(--white-color);
  border-radius: 15px;
}
.header__nav-menu {
  margin-right: auto;
}
.header__nav-list {
  max-width: 642px;
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.header__nav-list-item {
  margin: 0;
}
.header__nav-list-item:not(:last-child) {
  margin-right: 45px;
}
.header__menu-link {
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: var(--gray-color);
  transition: color 0.2s linear;
}
.header__menu-link:focus-visible {
  color: var(--light-brown-color);
  outline: 1px solid var(--light-brown-color);
  outline-offset: 5px;
}
.header__menu-link:active {
  color: var(--brown-color);
  outline: none;
}
.header__nav-button:not(:last-child) {
  margin-right: 40px;
}
.header__button-list {
  max-width: 327px;
  display: flex;
  flex-wrap: wrap;
}
.header__button {
  min-width: 100px;
  padding: 13px 20px;
  background-color: transparent;
  color: var(--light-brown-color);
  border: solid var(--light-brown-color) 1px;
  border-radius: 10px;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.2s linear;
}
.header__button:focus-visible {
  color: var(--white-color);
  background-color: var(--light-brown-color);
}
.header__button:active {
  color: var(--white-color);
  background-color: var(--brown-color);
}

.offers__content {
  min-height: 540px;
  display: flex;
}
.offers__card-block {
  display: flex;
  flex-direction: column;
  width: calc((100% - 24px) / 2);
}
.offers__card-block:not(:last-child) {
  margin-right: 24px;
}
.offers__card {
  position: relative;
  display: flex;
  flex-direction: column;
  flex: 1;
  padding: 40px 108px 40px 45px;
  background-color: var(--light-gray-color);
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 15px;
}
.offers__card:not(:last-child) {
  margin-bottom: 24px;
}
.offers__card-first {
  background-image: linear-gradient(to right, rgba(48, 64, 89, 0.84) 0%, rgba(53, 65, 83, 0) 66.67%), url("img/offer-card-first.jpg");
}
.offers__card-second {
  background-image: linear-gradient(to right, rgba(48, 64, 89, 0.84) 0%, rgba(53, 65, 83, 0) 66.67%), url("img/offer-card-second.jpg");
}
.offers__card-third {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  background-image: linear-gradient(to top, rgba(48, 64, 89, 0.84) 0%, rgba(53, 65, 83, 0) 66.67%), url("img/offer-card-third.jpg");
}
.offers__card-heading {
  margin: 0;
  margin-bottom: 15px;
  font-weight: 700;
  font-size: 40px;
  line-height: 40px;
  color: var(--white-color);
}
.offers__card-heading-third {
  font-size: 60px;
  line-height: 60px;
}
.offers__price {
  margin-bottom: 47px;
  font-weight: 400;
  font-size: 20px;
  line-height: 20px;
  color: var(--white-color);
  opacity: 0.8;
}
.offers__price-third {
  font-size: 30px;
  line-height: 30px;
}
.offers__more {
  display: inline-flex;
  align-items: center;
  max-width: 105px;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: var(--yellow-color);
  transition: color 0.2s linear;
}
.offers__more .offers__more-svg {
  transition: transform 0.2s ease;
}
.offers__more::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.offers__more:focus-visible::before {
  outline: 1px var(--light-brown-color) solid;
  outline-offset: 8px;
}
.offers__more:active::before {
  outline: none;
}
.offers__more:active {
  color: var(--white-color);
}
.offers__more:active .offers__more-svg {
  transform: none;
}
.offers__more-text {
  margin-right: 5px;
}

.about__paragraph {
  max-width: 888px;
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 32px;
}

.services__list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
}
.services__list-item {
  width: 573px;
  min-height: 330px;
  position: relative;
  margin: 0;
  margin-bottom: 24px;
  padding: 191px 126px 35px 45px;
  background-color: var(--white-color);
  border-radius: 15px;
  background-repeat: no-repeat;
  background-size: contain;
}
.services__list-item:nth-child(odd) {
  margin-right: 24px;
}
.services__list-item:nth-last-child(-n+2) {
  margin-bottom: 0;
}

.service__card-first {
  background-image: url("img/service-card-first.jpg");
}
.service__card-second {
  background-image: url("img/service-card-second.jpg");
}
.service__card-third {
  background-image: url("img/service-card-third.jpg");
}
.service__card-fourth {
  background-image: url("img/service-card-fourth.jpg");
}
.service__heading {
  margin: 0;
  font-weight: 500;
  font-size: 20px;
  line-height: 40px;
  color: var(--light-brown-color);
}
.service__link {
  display: inline-flex;
  align-items: center;
  stroke: var(--light-brown-color);
  transition: color 0.2s linear, stroke 0.2s linear;
}
.service__link::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.service__link:focus-visible::before {
  outline: 1px var(--light-brown-color) solid;
  outline-offset: 8px;
}
.service__link:active::before {
  outline: none;
}
.service__link .service__svg {
  transition: transform 0.2s ease;
}
.service__link:active {
  color: var(--brown-color);
  stroke: var(--light-brown-color);
}
.service__link:active .service__svg {
  transform: none;
}
.service__link-text {
  margin-right: 6px;
}
.service__desc {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 32px;
}

.benefits__list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
}

.benefit__card {
  width: 276px;
  min-height: 276px;
  margin: 0;
  margin-right: 22px;
  margin-bottom: 22px;
  padding: 116px 42px 32px 45px;
  background-color: var(--white-color);
  border-radius: 15px;
  background-repeat: no-repeat;
  background-position: 21% 19%;
  background-size: 67px 66px;
}
.benefit__card:nth-child(4n) {
  margin-right: 0;
}
.benefit__card:nth-last-child(-n+4) {
  margin-bottom: 0;
}
.benefit__card-first {
  background-image: url("img/bathtub-icon.svg");
}
.benefit__card-second {
  background-image: url("img/hanger-icon.svg");
}
.benefit__card-third {
  background-image: url("img/heating-icon.svg");
}
.benefit__card-fourth {
  background-image: url("img/hotel-key-icon.svg");
}
.benefit__card-fiveth {
  background-image: url("img/parking-icon.svg");
}
.benefit__card-sixth {
  background-image: url("img/reception-icon.svg");
}
.benefit__card-seventh {
  background-image: url("img/security-box-icon.svg");
}
.benefit__card-eighth {
  background-image: url("img/swimming-pool-icon.svg");
}
.benefit__desc {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 32px;
}

.accommodation__list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
}
.accommodation__card {
  width: 374px;
  min-height: 440px;
  margin: 0;
  margin-right: 24px;
  margin-bottom: 24px;
  background-color: var(--white-color);
  border-radius: 15px;
  overflow: hidden;
}
.accommodation__card:nth-child(3n) {
  margin-right: 0;
}
.accommodation__card:nth-last-child(-n+3) {
  margin-bottom: 0;
}
.accommodation__card-latest {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  padding-bottom: 27px;
  background-image: linear-gradient(to top, rgba(48, 64, 89, 0.84) 0%, rgba(53, 65, 83, 0) 66.67%), url("img/hotel-card-latest.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-color: var(--light-gray-color);
}

.hotel__img {
  object-fit: contain;
}
.hotel__info {
  display: flex;
  flex-direction: column;
  padding: 32px 45px 45px;
}
.hotel__worth {
  display: flex;
  align-items: flex-start;
  margin-bottom: 16px;
}
.hotel__min-price {
  margin-right: 4px;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: var(--light-gray-color);
}
.hotel__price {
  margin-right: 4px;
  font-weight: 700;
  font-size: 20px;
  line-height: 20px;
}
.hotel__per-night {
  margin-right: 52px;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
}
.hotel__star-icon {
  margin-right: 6px;
}
.hotel__star-icon:last-child {
  margin-right: 0;
}
.hotel__name {
  margin: 0;
  margin-bottom: 16px;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
}
.hotel__place {
  margin-bottom: 20px;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: var(--light-gray-color);
}
.hotel__all-options {
  display: inline-flex;
  align-items: center;
  font-weight: 500;
  font-size: 20px;
  line-height: 40px;
  color: var(--white-color);
}
.hotel__all-options::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.hotel__button {
  min-width: 100px;
  padding: 13px 20px;
  background-color: transparent;
  color: var(--light-brown-color);
  border: solid var(--light-brown-color) 1px;
  border-radius: 10px;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.2s linear;
}
.hotel__button:active {
  color: var(--white-color);
  background-color: var(--brown-color);
}
.hotel__button:focus-visible {
  color: var(--white-color);
  background-color: var(--light-brown-color);
}

.tour__wrapper {
  padding: 45px;
  border-radius: 15px;
  background-color: #E5E5E5;
}
.tour__heading {
  padding-left: 0;
}
.tour__form {
  display: flex;
  flex-wrap: wrap;
  padding: 32px 28px;
  border-radius: 15px;
  background-color: var(--white-color);
}
.tour__group {
  display: flex;
  flex-direction: column;
}
.tour__group:not(:last-child) {
  margin-right: 41px;
}
.tour__group:last-child {
  margin-right: 51px;
}
.tour__select-label {
  margin-bottom: 15px;
  font-weight: 400;
  font-size: 12px;
  line-height: 12px;
}
.tour__select-input {
  border: none;
  width: 169px;
}
.tour__select-input-second {
  width: 121px;
}
.tour__select-nights-amount {
  border-bottom: solid var(--dark-white-color) 1px;
}
.tour__select-tourists-amount {
  border-bottom: solid var(--dark-white-color) 1px;
}
.tour__select-btn {
  padding: 13px 30px;
}
.tour__button {
  min-width: 100px;
  padding: 13px 20px;
  background-color: transparent;
  color: var(--light-brown-color);
  border: solid var(--light-brown-color) 1px;
  border-radius: 10px;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.2s linear;
}
.tour__button:active {
  color: var(--white-color);
  background-color: var(--brown-color);
}
.tour__button:focus-visible {
  color: var(--white-color);
  background-color: var(--light-brown-color);
}

.contacts {
  margin-bottom: 40px;
}
.contacts__wrapper {
  padding: 45px 45px;
  display: flex;
  border-radius: 15px;
  background-color: var(--white-color);
}
.contacts__address {
  font-style: normal;
}
.contacts__list {
  margin: 0;
  margin-right: 52px;
  margin-bottom: 45px;
  padding: 0;
  list-style-type: none;
}
.contacts__list-item {
  margin: 0;
  padding-bottom: 23px;
  display: flex;
  border-bottom: var(--dark-white-color) solid 1px;
}
.contacts__list-item:not(:last-child) {
  margin-bottom: 25px;
}
.contacts__mail {
  max-width: 128px;
}
.contacts__link {
  color: var(--light-brown-color);
  transition: color 0.2s linear;
}
.contacts__link:focus-visible {
  color: var(--light-brown-color);
  outline: 1px solid var(--light-brown-color);
  outline-offset: 5px;
}
.contacts__link:active {
  color: var(--brown-color);
  outline: none;
}
.contacts__btn {
  min-width: 100px;
  padding: 27px 30px;
  background-color: transparent;
  color: var(--light-brown-color);
  border: solid var(--light-brown-color) 1px;
  border-radius: 10px;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.2s linear;
}
.contacts__btn:active {
  color: var(--white-color);
  background-color: var(--brown-color);
}
.contacts__btn:focus-visible {
  color: var(--white-color);
  background-color: var(--light-brown-color);
}
.contacts__img {
  width: 653px;
  object-fit: contain;
  border-radius: 10px;
}

.contact__name {
  max-width: 145px;
  min-width: 145px;
  margin: 0;
  font-weight: 700;
  font-size: 20px;
  line-height: 20px;
}
.contact__text {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}
.contact__mail-use {
  color: var(--light-gray-color);
}
.contact__content {
  display: flex;
  flex-direction: column;
}
.contact__content:not(:last-child) {
  margin-bottom: 3px;
}

.footer {
  padding-bottom: 30px;
}
.footer__wrapper {
  padding: 45px;
  display: flex;
  flex-wrap: wrap;
  background-color: var(--light-black-color);
  border-radius: 15px;
  background-image: url("img/footer-img.svg");
  background-repeat: no-repeat;
  background-position: right 177px bottom;
}
.footer__info {
  max-width: 302px;
  margin-right: 125px;
  list-style-type: none;
}
.footer__text {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 30px;
  color: var(--white-color);
}
.footer__company-text {
  margin-bottom: 37px;
}
.footer__info-text {
  margin-bottom: 40px;
}
.footer__contacts-item .footer__link {
  max-width: 143px;
}
.footer__contacts-item:nth-child(2) {
  margin-bottom: 15px;
}
.footer__link {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: var(--light-brown-color);
  transition: color 0.2s linear;
}
.footer__link:focus-visible {
  color: var(--light-brown-color);
  outline: 1px solid var(--light-brown-color);
  outline-offset: 5px;
}
.footer__link:active {
  color: var(--brown-color);
  outline: none;
}
.footer__contacts {
  max-width: 359px;
  margin-right: auto;
  list-style-type: none;
  font-style: normal;
}
.footer__contacts-content {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  list-style-type: none;
}
.footer__contacts-content:not(:last-child) {
  margin-bottom: 15px;
}
.footer__contacts-item {
  margin: 0;
}

.socials {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.socials__icon {
  color: var(--light-gray-color);
  transition: color 0.3s ease;
}
.socials__icon:focus-visible {
  color: var(--light-brown-color);
}
.socials__icon:active {
  color: var(--brown-color);
}
.socials__item:not(:last-child) {
  margin-bottom: 15px;
}